@import "../../global.scss";

.intro {
    display: flex;
    background-image: url(../../assets/FrancescoUngaroSky.webp);
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left {
        display: flex;
        flex: 0.4;
    }

    .right {
        flex: 0.6;
        position: relative;
        flex-direction: column;
        display: flex;
        justify-content: center;
        align-items: center;
        @include mobile{
            justify-content: start;

        }
  
        .avatar{
            height: 35%;
            width: auto;
            @include mobile{
                height: 25%;
                width: auto;
            }
        }

        .wrapper {
            display: flex;
            align-items: center;
            flex-direction: column;

            h1 {
                color: white;
                font-size: 3em;
                @include mobile{
                    font-size: 2em;
                }
            }

            h2 {
                color: white;
                font-size: 1em;
                text-align: center;
            }
            p{
                color:white;
                font-size: 1em;
            }
        }

        a {
            position: absolute;
            bottom: 1px;
            color: white;
            animation: arrowBlink 5s linear infinite;
        }

        @keyframes arrowBlink{
            50%{
                opacity: 0;
            }
        }
    }
}