@import "../../global.scss";

.projects {
    background-color: lightgrey;
    background-image: url(../../assets/ProjectBG.webp);
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-direction: column;

    .container h2{
        color: black;
        text-decoration: underline;
        text-decoration-color: $mainColor;
        text-align: center;
        font-weight: 700;
        font-size: 4em;
        
        @include mobile{
            font-size: 1.5em;
        }
    }

    .container {
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        @include mobile{
            width: 90%;
        }

        .button:hover{
            background-color: $mainColor;
            transition: all 1s ease;
        }

        .button{
            display: flex;
            border-radius: 30px;
            background-color: #51b1fd;
            padding: 10px;
            margin: 5px;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            
            .left{
                display: flex;
            }
            .left h1{
                color: white;
                margin-right: 50px;
                font-weight: 700;

                @include mobile{
                    font-size: 14px;
                    margin-right: 5px;
                }
                
            }
            .right{
                display: flex;
                flex-direction: row;

            }
            .right img{
                margin-right: 20px;
                width: 6vh;
                height: auto;
                @include mobile{
                    margin-right: 5px;
                    width: 4vh;
                }
            }
        }
    }
    .arrowdown a{
        position: absolute;
        color: white;
        bottom: 5px;
        animation: arrowBlink 5s linear infinite;
    }
    @keyframes arrowBlink{
        50%{
            opacity: 0;
        }
    }
}