@import "../../global.scss";
.contact {
    display: flex;
    background-color: #51b1fd;
    @include mobile{
        flex-direction: column;
    }

    .left {
        flex: .35;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        @include mobile{
            display: none;
        }

        .leftcontainer {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 90%;
            height: 80%;
            color: white;
            background-color: #0f548d;
            border-radius: 50px;
        }

        .leftcontainer h2 {
            font-size: 5em;
            font-weight: 700;
            @include mobile{
                font-size: 3em;
            }
        }

        .leftcontainer p {
            font-size: 1em;
            padding: 10px;
            @include mobile{
                display: none;
            }
        }

        .leftcontainer ul {
            margin-top: 10px;
            list-style: circle;
            text-align: left;
            @include mobile{
                display: none;
            }
        }
    }

    .right {
        flex: .65;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        background-color: lightblue;
       
        @include mobile{
            flex: .8;
        }

        form {
            width: 95%;
            height: 80%;
            flex-direction: column;
            display: flex;

            input {
                width: 94%;
                padding: 20px;
                font-size: 14px;
                margin-bottom: 15px;
                background-color: #0f548d;
                border-radius: 15px;
                border:none;
                color: white;
            }
            input::placeholder{
                color:white;
            }
            input:not(:placeholder-shown){
                background-color: #51b1fd;
            }
            input:invalid{
                color:red;
            }
            input:hover{
                background-color: #51b1fd;
                transition: all 1s ease;
            }

            textarea{
                width: 94%;
                padding: 20px;
                font-size: 14px;
                margin-bottom: 15px;
                flex:1;
                background-color: #0f548d;
                border-radius: 15px;
                border:none;
                color:white
            }
            textarea:hover{
                background-color: #51b1fd;
                transition: all 1s ease;
            }
            textarea:not(:placeholder-shown){
                background-color: #51b1fd;
            }
            textarea::placeholder{
                color: white;
            }
            button{
                width:50%;
                padding: 20px;
                background-color: #0f548d;
                color: white;
                border-radius: 15px;
                border: none;
                cursor: pointer;
            }
            button:hover{
                background-color: #51b1fd;
                transition: all 1.5s ease;
            }
        }
    }
    .contactinfo{
        display: none;
        flex-direction: column;

        @include mobile{
            display: flex;
            flex: 0.2;
            justify-content: center;
            background-color: $mainColor;
        }

        .itemcontainer{
            display: flex;
            align-items: center;
            color:white;
            .icon{
                font-size: 25px;
                margin-right:3px;
            }
            a{
                font-size: 1.2em;
                font-weight: 300;
                text-decoration: none;
                color:inherit;
            }
        }
    }
}