@import "../../global.scss";

.about {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .container {
        width: 90vw;
        height: 75vh;
        border-radius: 30px;
        display: flex;

        @include mobile {
            flex-direction: column;
            align-items: center;
        }

        .left {
            flex: 0.7;
            display: flex;
            align-items: center;
            justify-content: center;

            @include mobile {
                flex: 0;
            }

            .imgContainer {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                overflow: hidden;
            }

            .imgContainer img {
                width: 100%;
                height: auto;
                @include mobile{
                    display:none;
                }
            }

        }

        .right {
            flex: 0.3;
            display: flex;
            align-items: center;

            @include mobile {
                flex: 1;
            }

            .wrapper {
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 5px;
                @include mobile{
                    padding: 0px;
                }

                h1 {
                    color: black;
                    font-size: 4em;
                    @include mobile{
                        font-size: 3em;
                    }
                }

                h2 {
                    color: black;
                    font-size: 1.5em;

                }

                p {
                    color: black;
                    font-size: 1em;
                    text-align: center;
                }
            }

        }

    }

    .canvas {
        position: absolute;
        z-index: -1;
    }

    a {
        position: absolute;
        color: black;
        bottom: 5px;
        animation: arrowBlink 5s linear infinite;
    }

    @keyframes arrowBlink {
        50% {
            opacity: 0;
        }
    }
}